<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'单位转换'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-unit-conversion
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-unit-conversion>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditUnitConversion from "./create-or-edit-unit-conversion/create-or-edit-unit-conversion.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "UnitConversion",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditUnitConversion,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该单位转换规则吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const unitTypes = [
          "长度单位", 
          "重量单位", 
          "面积单位", 
          "体积单位", 
          "温度单位"
        ];
        
        const sourceUnits = [
          "米", "千克", "平方米", "立方米", "摄氏度"
        ];
        
        const targetUnits = [
          "厘米", "克", "平方厘米", "毫升", "华氏度"
        ];
        
        const statuses = ["启用", "禁用"];
        
        for (let i = 0; i < 25; i++) {
          const typeIndex = i % 5;
          let conversionRatio = 0;
          let conversionFormula = "";
          
          switch(typeIndex) {
            case 0: // 长度
              conversionRatio = 100;
              conversionFormula = "源单位 × 100";
              break;
            case 1: // 重量
              conversionRatio = 1000;
              conversionFormula = "源单位 × 1000";
              break;
            case 2: // 面积
              conversionRatio = 10000;
              conversionFormula = "源单位 × 10000";
              break;
            case 3: // 体积
              conversionRatio = 1000000;
              conversionFormula = "源单位 × 1000000";
              break;
            case 4: // 温度
              conversionRatio = 1.8;
              conversionFormula = "源单位 × 1.8 + 32";
              break;
          }
          
          dataList[i] = {
            id: i + 1,
            conversionCode: `UC${String(i + 1).padStart(4, "0")}`,
            name: `${sourceUnits[typeIndex]}转${targetUnits[typeIndex]}`,
            unitType: unitTypes[typeIndex],
            sourceUnit: sourceUnits[typeIndex],
            targetUnit: targetUnits[typeIndex],
            conversionRatio: conversionRatio,
            conversionFormula: conversionFormula,
            description: `${sourceUnits[typeIndex]}到${targetUnits[typeIndex]}的转换规则，适用于${unitTypes[typeIndex]}`,
            status: statuses[i % 2],
            createdBy: "admin",
            createdTime: "2024-03-18 " + (10 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.conversionCode.indexOf(this.filterText) > -1 || 
                   item.name.indexOf(this.filterText) > -1 ||
                   item.unitType.indexOf(this.filterText) > -1 ||
                   item.sourceUnit.indexOf(this.filterText) > -1 ||
                   item.targetUnit.indexOf(this.filterText) > -1 ||
                   item.conversionFormula.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style>