<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 转换编码 -->
            <a-form-model-item
              label="转换编码"
              prop="conversionCode"
              ref="conversionCode"
            >
              <a-input
                v-model="entity.conversionCode"
                placeholder="请输入转换编码，格式为UC开头加4位数字"
                name="conversionCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 转换名称 -->
            <a-form-model-item
              label="转换名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入转换名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status" name="status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 单位类型 -->
            <a-form-model-item
              label="单位类型"
              prop="unitType"
              ref="unitType"
            >
              <a-select
                v-model="entity.unitType"
                placeholder="请选择单位类型"
                name="unitType"
                @change="handleUnitTypeChange"
              >
                <a-select-option value="长度单位">长度单位</a-select-option>
                <a-select-option value="重量单位">重量单位</a-select-option>
                <a-select-option value="面积单位">面积单位</a-select-option>
                <a-select-option value="体积单位">体积单位</a-select-option>
                <a-select-option value="温度单位">温度单位</a-select-option>
                <a-select-option value="时间单位">时间单位</a-select-option>
                <a-select-option value="速度单位">速度单位</a-select-option>
                <a-select-option value="其他单位">其他单位</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          
          <a-col :sm="6">
            <!-- 源单位 -->
            <a-form-model-item
              label="源单位"
              prop="sourceUnit"
              ref="sourceUnit"
            >
              <a-select
                v-model="entity.sourceUnit"
                placeholder="请选择源单位"
                name="sourceUnit"
                :disabled="!entity.unitType"
              >
                <a-select-option
                  v-for="unit in sourceUnitOptions"
                  :key="unit.value"
                  :value="unit.value"
                >
                  {{ unit.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 目标单位 -->
            <a-form-model-item
              label="目标单位"
              prop="targetUnit"
              ref="targetUnit"
            >
              <a-select
                v-model="entity.targetUnit"
                placeholder="请选择目标单位"
                name="targetUnit"
                :disabled="!entity.unitType"
              >
                <a-select-option
                  v-for="unit in targetUnitOptions"
                  :key="unit.value"
                  :value="unit.value"
                >
                  {{ unit.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 是否反向可用 -->
            <a-form-model-item
              label="反向可用"
              prop="bidirectional"
              ref="bidirectional"
            >
              <a-switch v-model="entity.bidirectional" />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 转换比率 -->
            <a-form-model-item
              label="转换比率"
              prop="conversionRatio"
              ref="conversionRatio"
            >
              <a-input-number
                v-model="entity.conversionRatio"
                :min="0"
                :precision="6"
                style="width: 100%"
                placeholder="请输入转换比率"
                name="conversionRatio"
              />
            </a-form-model-item>
          </a-col>
         
          <a-col :sm="12">
            <!-- 转换公式 -->
            <a-form-model-item
              label="转换公式"
              prop="conversionFormula"
              ref="conversionFormula"
            >
              <a-input
                v-model="entity.conversionFormula"
                placeholder="请输入转换公式，如：源单位 × 100"
                name="conversionFormula"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 单位描述 -->
            <a-form-model-item 
              label="单位描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述"
                :rows="2"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 转换示例 -->
      <g-collapse headerName="转换示例">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-example-btn" type="primary" icon="plus" @click="addExample">添加示例</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="exampleColumns" 
                :dataSource="entity.examples" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="sourceValue" slot-scope="text, record, index">
                  <a-input-number 
                    v-model="entity.examples[index].sourceValue" 
                    :precision="3"
                    style="width: 100%"
                    placeholder="请输入源值" 
                  />
                </template>
                <template slot="targetValue" slot-scope="text, record, index">
                  <a-input-number 
                    v-model="entity.examples[index].targetValue" 
                    :precision="3"
                    style="width: 100%"
                    placeholder="请输入目标值" 
                  />
                </template>
                <template slot="note" slot-scope="text, record, index">
                  <a-input v-model="entity.examples[index].note" placeholder="请输入说明" />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="primary" 
                    icon="calculator" 
                    size="small" 
                    @click="() => calculateTargetValue(index)"
                    style="margin-right: 8px"
                  />
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeExample(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditUnitConversion",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        conversionCode: "",
        name: "",
        unitType: "",
        sourceUnit: "",
        targetUnit: "",
        conversionRatio: 1,
        conversionFormula: "",
        bidirectional: false,
        description: "",
        examples: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      sourceUnitOptions: [], // 源单位选项
      targetUnitOptions: [], // 目标单位选项
      unitMap: {
        "长度单位": [
          { label: "米", value: "米" },
          { label: "厘米", value: "厘米" },
          { label: "毫米", value: "毫米" },
          { label: "千米", value: "千米" },
          { label: "英寸", value: "英寸" },
          { label: "英尺", value: "英尺" }
        ],
        "重量单位": [
          { label: "千克", value: "千克" },
          { label: "克", value: "克" },
          { label: "毫克", value: "毫克" },
          { label: "吨", value: "吨" },
          { label: "磅", value: "磅" }
        ],
        "面积单位": [
          { label: "平方米", value: "平方米" },
          { label: "平方厘米", value: "平方厘米" },
          { label: "平方千米", value: "平方千米" },
          { label: "公顷", value: "公顷" },
          { label: "亩", value: "亩" }
        ],
        "体积单位": [
          { label: "立方米", value: "立方米" },
          { label: "立方厘米", value: "立方厘米" },
          { label: "升", value: "升" },
          { label: "毫升", value: "毫升" },
          { label: "加仑", value: "加仑" }
        ],
        "温度单位": [
          { label: "摄氏度", value: "摄氏度" },
          { label: "华氏度", value: "华氏度" },
          { label: "开尔文", value: "开尔文" }
        ],
        "时间单位": [
          { label: "秒", value: "秒" },
          { label: "分钟", value: "分钟" },
          { label: "小时", value: "小时" },
          { label: "天", value: "天" },
          { label: "周", value: "周" }
        ],
        "速度单位": [
          { label: "米/秒", value: "米/秒" },
          { label: "千米/小时", value: "千米/小时" },
          { label: "英里/小时", value: "英里/小时" },
          { label: "节", value: "节" }
        ],
        "其他单位": []
      },
      rules: {
        conversionCode: [
          { required: true, message: "请输入转换编码", trigger: "change" },
          { pattern: /^UC\d{4}$/, message: "转换编码必须为UC开头加4位数字", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入转换名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "转换名称不能包含前后空格", trigger: "change" }
        ],
        unitType: [
          { required: true, message: "请选择单位类型", trigger: "change" }
        ],
        sourceUnit: [
          { required: true, message: "请选择源单位", trigger: "change" }
        ],
        targetUnit: [
          { required: true, message: "请选择目标单位", trigger: "change" }
        ],
        conversionRatio: [
          { required: true, message: "请输入转换比率", trigger: "change" },
          { type: "number", min: 0, message: "转换比率不能小于0", trigger: "change" }
        ],
        conversionFormula: [
          { required: true, message: "请输入转换公式", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ]
      },
      exampleColumns: [
        {
          title: "源值",
          dataIndex: "sourceValue",
          key: "sourceValue",
          width: "25%",
          scopedSlots: { customRender: "sourceValue" }
        },
        {
          title: "目标值",
          dataIndex: "targetValue",
          key: "targetValue",
          width: "25%",
          scopedSlots: { customRender: "targetValue" }
        },
        {
          title: "说明",
          dataIndex: "note",
          key: "note",
          width: "30%",
          scopedSlots: { customRender: "note" }
        },
        {
          title: "操作",
          key: "action",
          width: "20%",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取单位转换数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        conversionCode: "",
        name: "",
        unitType: "",
        sourceUnit: "",
        targetUnit: "",
        conversionRatio: 1,
        conversionFormula: "",
        bidirectional: false,
        description: "",
        examples: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
      this.sourceUnitOptions = [];
      this.targetUnitOptions = [];
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          ...row,
          // 确保examples数组被正确初始化
          examples: row.examples || []
        };
        
        // 如果有单位类型，初始化单位选项
        if (row.unitType) {
          this.handleUnitTypeChange(row.unitType);
        }
      }
    },
    
    async save() {
      // 保存校验
      let res = await this.checkError();
      if (res) {
        // 检查转换关系
        if (this.entity.sourceUnit === this.entity.targetUnit) {
          this.$message.error("源单位和目标单位不能相同");
          return;
        }

        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 处理单位类型变更
    handleUnitTypeChange(value) {
      // 更新源单位和目标单位选项
      if (this.unitMap[value]) {
        this.sourceUnitOptions = [...this.unitMap[value]];
        this.targetUnitOptions = [...this.unitMap[value]];
        
        // 清空已选的源单位和目标单位
        if (this.entity.unitType !== value) {
          this.entity.sourceUnit = "";
          this.entity.targetUnit = "";
        }
      } else {
        this.sourceUnitOptions = [];
        this.targetUnitOptions = [];
      }
    },
    
    // 添加转换示例
    addExample() {
      this.entity.examples.push({
        id: Date.now(), // 使用时间戳作为临时ID
        sourceValue: 1,
        targetValue: null,
        note: ""
      });
    },
    
    // 删除转换示例
    removeExample(index) {
      this.entity.examples.splice(index, 1);
    },
    
    // 计算目标值
    calculateTargetValue(index) {
      const example = this.entity.examples[index];
      if (example && this.entity.conversionRatio > 0) {
        example.targetValue = example.sourceValue * this.entity.conversionRatio;
        
        // 更新说明
        if (!example.note) {
          example.note = `${example.sourceValue} ${this.entity.sourceUnit} = ${example.targetValue} ${this.entity.targetUnit}`;
        }
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-example-btn {
  margin-bottom: 16px;
}
</style> 